import ReactGA from 'react-ga';
import _merge from 'lodash/merge';
import _get from 'lodash/get';
import Session from './session';
import config from '../config';

ReactGA.initialize(config.googleAnalyticsId);

const Tracker = {
  prepareTrackUser() {
    const user = Session.getUserData();
    if (user && _get(user, 'member.id')) {
      const trackUser = {
        app_id: config.appNamespace,
        app_name: config.appName,
        user_id: _get(user, 'member.id'),
        email: _get(user, 'member.email'),
        phone: _get(user, 'member.phone'),
        name: `${_get(user, 'member.name')} ${_get(user, 'member.lastname')}`,
        company: _get(user, 'member.company.name'),
        company_id: _get(user, 'member.company.id'),
        created_at: _get(user, 'member.created_at'),
        verified_at: _get(user, 'member.verified_at')
      };
      return trackUser;
    }
    return {};
  },
  hit(event, props) {
    if (event && config.isProduction) {
      const trackUser = this.prepareTrackUser();
      const trackProps = _merge(props, trackUser);
      ReactGA.set(trackProps);
      ReactGA.event({ category: 'User', action: event });
    }
  },
  trackPageView(screen) {
    if (screen && config.isProduction) {
      const trackUser = this.prepareTrackUser();
      ReactGA.set(trackUser);
      ReactGA.event({ category: 'User', action: `${screen} - Viewed` });
      ReactGA.pageview(screen);
    }
  }
};

export default Tracker;
